import { Injectable } from '@angular/core';
import { from, Observable } from 'rxjs';
import { of } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { catchError } from 'rxjs/operators';
import { UserModel } from '../models/user';

@Injectable({
  providedIn: 'root',
})
export class UserService {
  constructor(private http: HttpClient) {}
  private dataURL = 'http://localhost:3001/user';

  httpOptions = {
    headers: new HttpHeaders({ 'Content-Type': 'application/json' }),
  };

  getData(): Observable<UserModel[]> {
    return this.http
      .get<UserModel[]>(this.dataURL)
      .pipe(catchError((error) => of([])));
  }

  searchUserName(typedString: string): Observable<UserModel[]> {
    if (!typedString) {
      return of([]);
    }
    return this.http
      .get<UserModel[]>(`${this.dataURL}?name_like=${typedString}`)
      .pipe(catchError((error) => of(null)));
  }
  
  insertData(data: UserModel): Observable<UserModel> {
    return this.http
      .post<UserModel>(this.dataURL, data, this.httpOptions)
      .pipe(catchError((error) => of(new UserModel(null,null))));
  }
}
