import { Component, OnInit } from '@angular/core';
import { UserService } from '../services/user.service';
import { UserModel } from '../models/user';
import { ThrowStmt } from '@angular/compiler';
@Component({
  selector: 'app-log-in',
  templateUrl: './log-in.component.html',
  styleUrls: ['./log-in.component.css'],
})
export class LogInComponent implements OnInit {
  user: string;
  password: string;
  userList: UserModel[];
  flag = false;
  constructor(private userService: UserService) {}
  CheckUser() {
    return this.userList.findIndex(
      (data) => this.user === data.username && this.password === data.password
    );
  }
  getUserList() {
    this.userService.getData().subscribe((user) => (this.userList = user));
  }
  btnDangNhap() {
    if (!this.user || !this.password) {
      alert('Bạn cần điền đủ thông tin trước khi đăng nhập');
    } else {
      this.flag = this.CheckUser() !== -1;
      if(!this.flag){
        alert("sai mật khẩu hoặc tài khoản");
      }
    }
  }
  ngOnInit(): void {
    this.getUserList();
  }
  LogOut(){
    this.flag = false;
  }
}
