import { Component, OnInit } from '@angular/core';
import { UserModel } from '../models/user';
import { UserService } from '../services/user.service';

@Component({
  selector: 'app-sign-up',
  templateUrl: './sign-up.component.html',
  styleUrls: ['./sign-up.component.css'],
})
export class SignUpComponent implements OnInit {
  user_name: string;
  pass_word: string;
  password_confirm: string;
  userList: UserModel[];
  constructor(private userService: UserService) {}

  SignUp() {
    if (!this.user_name || !this.pass_word || !this.password_confirm) {
      alert('nhập thiếu');
    } else if (this.pass_word !== this.password_confirm) {
      alert('password không giống nhau');
    } else {
      if (this.checkUser()) {
        alert('Đã có user');
      } else {
        const newUser: UserModel = new UserModel(
          this.user_name,
          this.pass_word
        );
        this.userService.insertData(newUser).subscribe();
        alert('thành công');
      }
    }
  }
  checkUser(): boolean {
    return (
      this.userList.findIndex((user) => user.username === this.user_name) != -1
    );
  }
  getData() {
    this.userService.getData().subscribe((data) => (this.userList = data));
  }
  ngOnInit(): void {
    this.getData();
  }
}
