import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { of } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { catchError } from 'rxjs/operators';
import { PokemonModel } from '../models/pokemon';

@Injectable({
  providedIn: 'root',
})
export class PokemonService {
  constructor(private http: HttpClient) {}
  private dataURL = 'http://localhost:3000/list';

  httpOptions = {
    headers: new HttpHeaders({ 'Content-Type': 'application/json' }),
  };

  getData(): Observable<PokemonModel[]> {
    return this.http
      .get<PokemonModel[]>(this.dataURL)
      .pipe(catchError((error) => of([])));
  }

  getDataById(id: number): Observable<PokemonModel> {
    const URL = this.dataURL + '/' + id;
    return this.http
      .get<PokemonModel>(URL)
      .pipe(catchError((error) => of(new PokemonModel())));
  }

  searchPokemon(typedString: string): Observable<PokemonModel[]> {
    if (!typedString.trim()) {
      return of([]);
    }
    return this.http
      .get<PokemonModel[]>(`${this.dataURL}?name_like=${typedString}`)
      .pipe(catchError((error) => of(null)));
  }
}
